@import url(https://fonts.googleapis.com/css?family=Lato:100&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.container {
  background: #f5ce79;
  overflow: hidden;
  font-family: 'Lato', sans-serif;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.center {
  -webkit-align-items: center;
          align-items: center;
  width: 180px;
  height: 60px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.btn {
  width: 500px;
  height: 80px;
  border-radius: 4px;
  background-color: #0cb127;
  cursor: pointer;
  outline: none;
  margin-top: 30px;
}

.btn:hover {
  background-color: #14992a;
}

.btn span {
  color: white;
  font-size: 28px;
  font-weight: 700;
}

input {
  margin: 10px 0;
  width: 500px;
  height: 60px;
  border-radius: 4px;
  padding-left: 16px;
  outline: auto;
  font-size: 28px;
}


.logo {
  width: 800px;
  margin-bottom: 50px;
}

.header {
  display: -webkit-flex;
  display: flex;
  padding: 20px;
  background-color: #f5ce79;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 100px;
  width: 100%;
}

.icons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 26px;
}

.icons svg{
  margin: 6px;
  cursor: pointer;
}

.icons svg:hover{
  color: rgb(30, 115, 226);
}

.logoHeader {
  width: 200px;
  height: 60px;
}
/* Add a black background color to the top navigation */
.topnav {
  background-color: #333;
  overflow: hidden;
}

/* Style the links inside the navigation bar */
.topnav a {
  float: left;
  display: block;
  color: #f2f2f2;
  font-weight: 800;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

/* Add an active class to highlight the current page */
.active {
  background-color: rgb(30, 115, 226);
  color: white;
}

/* Hide the link that should open and close the topnav on small screens */
.topnav .icon {
  display: none;
}

/* Add a dark background on topnav links and the dropdown button on hover */
.topnav a:hover, .dropdown:hover .dropbtn {
  background-color: #555;
  color: white;
}

.rooms {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  list-style: none;
}

.rooms li {
  margin: 10px;
  font-size: 16px;

}

.hr {
  width: 100%;
  height: 10px;
  background-color: #f3ab0e;
}

button {
  border: none;
  background-color: white;
  cursor: pointer;
}

button:hover {
  background-color: rgb(241, 216, 160);
}

.bed {
  font-size: 26px;
}

.actives {
  background-color: rgb(241, 216, 160);
}

.guests-card {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 30px;
  background-color: #f5ce79;
}

.guest-card {
  width: 500px;
  height: 500px;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  cursor: pointer;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.guest-card:hover {
  background-color: cornflowerblue;
}

.guest-card img{
  width: 300px;
  max-height: 400px;
}
body{
  background-color: #f7f7f7;
}

.not-found {
  background: url("https://i.pinimg.com/originals/11/dc/96/11dc96d2e4daca3ea8ff6aa41299b5e1.gif");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  height: 100vh;
  width: 100vw;
}


